.Container {
	overflow-y: auto;
	height: 100vh;
}

.Buttons {
	display: flex;
	justify-items: center;
	justify-content: space-around;
	align-items: center;

	padding: 40px 0 20px 0;
}

.Button {
	width: 18%;
	min-width: 280px;
	height: 80px;

	display: flex;
	justify-items: center;
	justify-content: center;
	align-items: center;

	background-color: #013e5590;
	color: white;
	font-size: 22px;

	border-radius: 4px;

	transition: all 0.4s ease;
}

.Button:hover {
	cursor: pointer;
	background-color: #013e55;
	transform: scale(1.02);
}

.Selected {
	background-color: #013e55;
	transform: scale(1.04) !important;
}
